const changeBrightness = (color: string, amount: number) => {
  return `#${color
    .replace(/^#/, '')
    .replace(/../g, (colorReduce: string) =>
      `0${Math.min(
        255,
        Math.max(0, parseInt(colorReduce, 16) + amount)
      ).toString(16)}`.substr(-2)
    )}`;
};
export default changeBrightness;
